body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.App {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  
  box-sizing: border-box;
}
.react-chatbot-kit-chat-message-container {
  background: white;
  border: 1px solid black;
  height: 500px;
  overflow-y: scroll;
  color: white;
}

.react-chatbot-kit-chat-container {
  position: absolute; 
  bottom: 10px; right: 30px;;
  width: 400px;
}

.react-chatbot-kit-chat-input-form {
  background: white;
  border: 1px solid black;
  color: white;
}

.react-chatbot-kit-chat-bot-message-container {
  display: flex;
  margin: 15px;
}

.react-chatbot-kit-user-chat-message-container {
  display: flex;
  margin: 15px;
  align-content: flex-end;
  justify-content: flex-end; 
}


.react-chatbot-kit-chat-bot-message {
  border-radius: 10px;
  padding: 8px;
  font-size: 12px;
  margin-right: 50px;
}
.react-chatbot-kit-user-chat-message {
  align-content: flex-end;
  background-color: #7a7a7ad0;
  border-radius: 10px;
  padding: 8px;
  font-size: 12px;
  margin-left: 50px;
  justify-content: flex-end; 
}

.user-avatar {
  align-content: flex-end;
  justify-content: flex-end; 
  align-items: left;
  
}
.react-chatbot-kit-chat-input-container {
  display: flex;
  width: 400px;
  background-color: black;
  border-radius: 0 0 5px 5px;
}
.react-chatbot-kit-chat-input-form {
  margin: auto;
  display: flex;
  flex-direction:row;
  width: 300px;
  background-color: black;
  height: 60px;
}

.react-chatbot-kit-chat-input {
  margin: auto;
  width: 300px;
  height: 40px;
  border-radius: 5px;
  
}
.react-chatbot-kit-chat-btn-send {
  margin: auto;
  width: 40px;
  background-color: white;
  height: 40px;
  margin-left: 10px;
}
svg{
  width: 20px;
  height:20px;
  padding-right: 3px;
  fill: black;
}


.my-avatar {
  width: 50px;
  height: 50px;
  margin-right: 8px;
}

.quiz-container {
    background: #28334aff;
    padding: 1rem 1rem;
    border-radius: 0.25rem;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
    transition: all 0.3s linear;
    text-align: center;
    font-size: 0.9rem;
    color: white;
  }
  .prev-btn,
  .next-btn {
    font-size: 0.99rem;
    background: transparent;
    border-color: transparent;
    cursor: pointer;
    color: white;
  }
  button {
    font-size: 0.99rem;
    font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
    background: transparent;
    border-color: #28334aff;
    padding: 0.4em;
    margin: 0.4em;
    cursor: pointer;
    border-radius: 20px;
  }
  .score {
    background: #28334aff;
    color: white;
    padding: 1.1rem;
    border-radius: 0.25rem;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
    transition: all 0.3s linear;
    text-align: center;
    font-size: 0.9rem;
  }
  
.user-avatar {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  margin-left: 12.5px;
  background-color: white;
  display: flex;
  justify-content: center;
  align-items: center;
}

.wrapper-class {
  padding: 15px;
  border: 1px solid #ccc;
  height: 490px;

}

.editor-class {
  padding: 15px;
  border: 1px solid #ccc;
  height: 400px;
}
.toolbar-class {
  border: 1px solid #ccc;
}
